
.cg-container {
  position: relative;
  width: 300px; /* Adjust size */
  height: 400px;
  margin: 10px auto; /* Center horizontally */
  perspective: 1000px;
  pointer-events: none; /* Let clicks pass through container area if needed, but enable for card */
}

.flip-card {
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  cursor: pointer;
  pointer-events: auto; /* Re-enable pointer events for the card itself */
}

.flip-card.flipped {
  transform: rotateY(180deg);
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.flip-card-front img, .flip-card-back img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    border-radius: 10px; /* Optional rounded corners */
    box-shadow: 0 4px 8px rgba(0,0,0,0.3); /* Optional shadow */
}

.flip-card-back {
  transform: rotateY(180deg);
}
